  width: 100%;
  background-color: #f6f6f6;
  margin: 0 auto;
  padding: 50px 25px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
}

header {
  justify-content: center;
  display: flex;
  position: relative;
  top: 6vh;
}

header .clip {
  height: 10rem;
  width: 10rem;
  filter: drop-shadow(10px 7px 5px #0000004d);
  background-image: url("../../clip.aa7825a3.png");
  background-position: center;
  background-size: cover;
  transform: rotate(8deg);
}

main {
  background-color: #f5f5f3;
  padding: 5em 2em;
  box-shadow: 10px 8px 25px 5px #0003;
}

.title {
  height: 10vh;
  border-bottom: 2px solid #000;
  justify-content: space-between;
  align-self: flex-end;
  padding: 2vh 0;
  font-weight: 600;
  display: flex;
}

