  margin-top: 2vh;
  font-size: .7rem;
  display: flex;
}

.detail {
  line-height: 1.3;
}

.detail:nth-child(1) {
  flex-grow: .5;
}

.detail:nth-child(2) {
  flex-grow: 2;
}

.detail h6 {
  font-weight: 600;
}

